	module.exports = {"data":{"markdownRemark":{"html":"<h3>JavaScript And The Web</h3>\n<p><a href=\"http://raganwald.com/2017/12/14/73939133.html\">73,939,133 / Truncatable Primes | Reg Braithwaite</a> - A nice writeup about how brute force can be useful in solving a <a href=\"https://projecteuler.net/\">Project Euler</a> style math problem.</p>\n<h3>Programming Tools and Practices</h3>\n<p><a href=\"http://facebook.github.io/jest/blog/2017/12/18/jest-22.html\">Jest 22: Refinements &#x26; Custom Runners | Jest Blog</a> - This looks like a solid refinement release for Jest.  One warning: If you’re running <code>jest -o</code> on precommit hooks as I described <a href=\"https://benmccormick.org/2017/02/26/running-jest-tests-before-each-git-commit/\">here</a>, Jest 22 fails when run with 0 tests, so you need to specify the <code>--passWithNoTests</code> flag in order to let things go when you make a change that doesn’t affect any tests.</p>\n<p><a href=\"https://www.gatsbyjs.org/blog/2017-12-20-introducing-the-gatsby-ux-research-program/\">Introducing The Gatsby UX Research Program | Gatsby Blog</a> - An unusual case of doing real UX research for an open source developer tool.  Very cool stuff.</p>\n<h3>Non-Tech</h3>\n<p><a href=\"https://www.justinobeirne.com/google-maps-moat\">Google Maps’s Moat</a> - If you have any interest in product design, maps, or computer intelligence, this is the one article you need to read this week.  Beautifully put together.  It’s an in depth examination of the difference between Google and Apple maps, but it reads as a deeper exploration of how big data combined with computer learning and automation can have a huge impact on product design.</p>\n<p><a href=\"https://izbicki.me/blog/how-to-cheat-at-settlers-of-catan-by-loading-the-dice-and-prove-it-with-p-values.html\">How to cheat at settlers by loading the dice | Mike Izbicki</a> - This is just a fun application of statistics to my favorite board game.</p>","frontmatter":{"title":"Weekly Links: December 23rd 2017","keywords":"primes jest gatsby google maps settlers","category":"opinion","date":"2017/12/23","path":"/2017/12/23/weekly-links-12-23-17/","layout":"weekly-links","hideFooter":null},"fields":{"slug":"/2017/12/23/weekly-links-12-23-17/"}}},"pathContext":{"slug":"/2017/12/23/weekly-links-12-23-17/","relatedPosts":[{"path":"/2013/06/09/90-done-halfway-there","data":{"title":"90% Done, Halfway There","path":"/2013/06/09/90-done-halfway-there","description":"A post on the difficulties of estimating development projects","date":"2013-06-09T18:45:00+00:00"}},{"path":"/2013/01/25/career-fairs-how-to-not-get-hired-and-how-to-give-yourself-a-chance","data":{"title":"Career Fairs: How To Not Get Hired (And How To Give Yourself A Chance)","path":"/2013/01/25/career-fairs-how-to-not-get-hired-and-how-to-give-yourself-a-chance","description":"What to do when you want a job","date":"2013-01-25T19:30:00+00:00"}},{"path":"/2013/06/08/customer-culture-revisited","data":{"title":"Customer Culture Revisited","path":"/2013/06/08/customer-culture-revisited","description":"Talking about how Microsoft culture impacts their products","date":"2013-06-08T18:45:00+00:00"}}]}}

